@font-face {
  font-family: "Comfortaa";
  src: url("../fonts/Comfortaa-Regular.woff2") format("woff2"), url("../fonts/Comfortaa-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Regular.woff2") format("woff2"), url("../fonts/Raleway-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-size: 20px;
  font-weight: 400;
  font-style: normal;
  line-height: 23px;
  font-family: "Raleway", "Arial", sans-serif;
  display: block;
  border: none;
  text-decoration: none;
  padding: 17px 0;
  cursor: pointer;
  box-sizing: border-box;
  background-color: #6a107a;
  color: #fbfcfe;
  width: 100%;
  border-radius: 40px;
  text-align: center;
  letter-spacing: 1.2px;
}
.button:hover {
  background-color: #b22ec8;
}
.button:active {
  background-color: rgba(106, 16, 122, 0.6);
}
